var $ = require('zepto');

var mTpl = require('../../tpl/screen_promotion.html');
var $tpl = $(mTpl);

var mExp = {};

var mElement = {
    $body: $(document.body),
    $closeBtn: $tpl.find('.btn-close-screen'),
    $btnJump: $tpl.find('.btn-jump'),
    $descWrapper: $tpl.find('.desc-wrapper'),
    $imgWrapper: $tpl.find('.img-wrapper')
};

function initView() {
    mElement.$body.append($tpl);
    mElement.$btnJump.text('@@i18n.activity_get_reward_btn_1');
    mElement.$imgWrapper.html('<img src="/common/imgs/mall/treasure/screen_promotion_img_002.png" alt="">');
}

function initEvent() {
    $tpl.on('click', function(evt) {
        hide();
    });
}

function show(reward=0) {
    mElement.$descWrapper.html('<p>@@i18n.activity_get_reward_desc_1</p>'.replace('%1$s', '<span class="yellow">' + reward + '</span>'));
    $tpl.show();
}

function hide() {
    $tpl.hide();
}

mExp.show = show;
mExp.init = function() {
    initView();
    initEvent();
};

module.exports = mExp;



