// simple cross domain ajax request, support FormData

/*
 * @param
 * opt = {
 *     url: '',
 *     type: '',
 *     timeout: '',
 *     data: '',
 *     wsc_sid: '',
 *     success: '',
 *     error: ''
 * }
 * */
function ajax(opt) {
    var xhr = new XMLHttpRequest();
    xhr.open(opt.type || 'GET', opt.url, true);
    xhr.setRequestHeader('Content-type', isFormData(opt.data) ? 'multipart/form-data' : 'application/json');
    if (opt.wsc_sid) {
        xhr.setRequestHeader('Authorization', opt.wsc_sid);
    }
    xhr.timeout = opt.timeout || 25000;
    xhr.onload = function(event) {
        var resXhr = event.target;
        if (resXhr.status == 200) {
            opt.success && opt.success(JSON.parse(resXhr.responseText || '{}'), resXhr);
        } else {
            opt.error && opt.error('error', resXhr);
        }
    };
    xhr.onerror = function(event) {
        opt.error && opt.error(event.type, event.target);
    };
    xhr.ontimeout = function(event) {
        opt.error && opt.error(event.type, event.target);
    };
    xhr.send(isFormData(opt.data) ? opt.data : JSON.stringify(opt.data));
}

function isFormData(data) {
    return (data || {}).constructor == FormData;
}

module.exports.ajax = ajax;



