var $ = require('zepto');
function notifyAnimate(data){
    var $wrap = $('.notify-txt'),
        $text = $('.notify-txt span');
    $text.html(data);
    var transitionClass = 'transition',
        ww = $wrap.width(),
        tw = $text.width()+40;
    $text.removeClass(transitionClass)
    $text.css({
        '-webkit-transform': 'translateX(' + ww + 'px)',
        'transform': 'translateX(' + ww + 'px)'
    });
    setTimeout(function(){
        $text.addClass(transitionClass).css({
            '-webkit-transform': 'translateX(-' + tw + 'px) translate3d(0,0,0)',
            'transform': 'translateX(-' + tw + 'px) translate3d(0,0,0)'
        })
    },30);
}


module.exports = {
    go: notifyAnimate
};


