var util = require('./util.js');

var URL_LIVE = 'http://infoc2.duba.net/g/v1/';
var URL_TEST = 'http://infoc2.duba.net/g/v1/';

function INFOC(tableId, options) {
    options = options || {};
    var self = this;
    self._params = {
        uuid: options.uuid || getUid(),
        xaid: options.xaid || getUid(),
        ver: parseInt(options.ver || '0', 10) || 0,
        mcc: options.mcc || '0',
        mnc: options.mnc || '0',
        cn: options.cn || '0',
        cn2: options.cn2 || '0',
        cl: options.cl || '0',
        prodid: options.prodid || '0',
        osver: options.osver || '0',
        brand: options.brand || '0',
        model: options.model || '0',
        romver: options.romver || '0',
        subver: options.subver || '0',

        product_no: '17',
        public_index: '5',
        business_index: tableId
    };
    self._reportList = null;
}

INFOC.prototype = {
    constructor: INFOC,
    addData: function(obj) {
        this._reportList = obj || {};
        return this;
    },
    reset: function() {
        this._reportList = null;
        return this;
    },
    report: function() {
        var base = URL_LIVE;
        if (window.location.hostname == 'ctest.cmcm.com') {
            base = URL_TEST;
        }

        var url = base + '?';
        url += stringify(this._params);
        url += '&';
        url += stringify(this._reportList);

        var img = new Image();
        img.src = url;
        this.reset();

        return this;
    }
};

function stringify(obj) {
    if (!obj) {
        return '';
    }
    var result = '';
    for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
            result += '&' + key + '=' + obj[key];
        }
    }
    return result.replace(/^&/, '');
}

function getUid() {
    if (!window.localStorage) {
        return '00000000';
    }
    var recorded = localStorage.getItem('wsc_key_report_uid');
    if (!recorded) {
        recorded = util.genUid(32);
        try {
            localStorage.setItem('wsc_key_report_uid', recorded);
        } catch (err) {

        }
    }

    return recorded.replace(/\W/g, '').slice(0, 8);
}

module.exports.INFOC = INFOC;


