function request(url, data, success, error, timeoutCallback) {
    mAjax.ajax({
        url: url,
        type: 'POST',
        data: data,
        wsc_sid: mWscSid || '',
        timeout: 15000,
        success: function(data, xhr) {
            data = data || {};
            var timestamp = getServerTimestamp(xhr);
            if (data.error == 0) {
                success && success(data, timestamp);
            } else {
                error && error(data, xhr.status);
            }
        },
        error: function(err, xhr) {
            if (err === 'timeout' && timeoutCallback) {
                timeoutCallback({error: -2}, xhr.status);
            } else {
                error && error({error: -1}, xhr.status);
            }
        }
    });
}

function getServerTimestamp(xhr) {
    try {
        return new Date(xhr.getResponseHeader('Date')).getTime();
    } catch (error) {
        return 0;
    }
}

function setWscSid(sid) {
    mWscSid = sid;
}

