var $ = require('zepto');

var tpl = require('../../tpl/my_credits.html');
var $tpl = $(tpl);


function init(balance) {
    $tpl.find('i').html(balance);
    $('.my-credits').length || $(document.body).append($tpl);
    $tpl.show();
}
function update(balance) {
    $('.my-credits').find('i').html(balance);
}

module.exports = {
    init: init,
    update: update,
};


