var $ = require('zepto');

var mCommon = require('com.cmcm.cloud');
var mStorage = require('../../../../../../node_modules/com.cmcm.cloud/src/storage.js');
var mTpl = require('../../tpl/screen_promotion.html');
var $tpl = $(mTpl);

var mHasShow = getHasShown();

var mExp = {};

var mElement = {
    $body: $(document.body),
    $closeBtn: $tpl.find('.btn-close-screen'),
    $btnJump: $tpl.find('.btn-jump'),
    $descWrapper: $tpl.find('.desc-wrapper'),
    $imgWrapper: $tpl.find('.img-wrapper')
};

function initView() {
    mElement.$body.append($tpl);

    mElement.$btnJump.text('@@i18n.screen_promotion_btn_check');
    mElement.$descWrapper.html('<p>@@i18n.screen_promotion_desc_1</p>');
    mElement.$imgWrapper.html('<img src="/common/imgs/mall/treasure/screen_promotion_img_001.png" alt="">');
}

function initEvent() {
    mElement.$closeBtn.on('click', function() {
        hide();
    });
    mElement.$btnJump.on('click', function() {
        hide();
        location.href = '../vouchers/mine.html';
    });
    $tpl.on('click', function(evt) {
        if (evt.target == evt.currentTarget) {
            hide();
        }
    });
}

function show() {
    if (mHasShow == '1') {
        return;
    }
    $tpl.show();
    setHasShown();
}

function hide() {
    $tpl.hide();
}

function setHasShown() {
    mStorage.session.setItem('activity_screen_promotion_shown', '1');
}

function getHasShown() {
    return  mStorage.session.getItem('activity_screen_promotion_shown');
}

mExp.getHasShow = getHasShown;
mExp.show = show;
mExp.init = function() {
    initView();
    initEvent();
};

module.exports = mExp;



