var mExp = {
    isTest: function() {
        var hostname = window.location.hostname;
        return hostname == 'ctest.cmcm.com';
        // return true;
    },
    getQueryVal: function(key, url) {
        var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
        var s = (url && '?' + url.split('?')[1]) || location.search;
        var r = s.slice(1).match(reg);
        if (r != null) {
            return decodeURIComponent(r[2]);
        }
        return null;
    },
    getBrowserLang: function() {
        var language = navigator.language;
        var languages = navigator.languages;

        if (!language && languages && languages.length > 0) {
            language = languages[0];
        }

        if (!language) {
            language = 'en';
        }

        language = language.toLowerCase();

        return language;
    },
    genUid: function(count) {
        count = parseInt(count || 1, 10) || 1;
        if (count < 1) {
            count = 1;
        }
        if (count > 512) {
            count = 512;
        }
        var charArr = [];
        for (var i = 0; i < count; i++) {
            charArr[i] = 'x';
        }
        return charArr.join('').replace(/[xy]/g, function(c) {
            var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
            return v.toString(16);
        });
    },
    openMarket: function(pkgName, referrer) {
        var pre = Date.now();

        var marketUrl = 'market://details?id=' + pkgName + (referrer ? ('&referrer=' + referrer) : '');
        var httpsUrl = 'https://play.google.com/store/apps/details?id=' + pkgName +  (referrer ? ('&referrer=' + referrer) : '');

        var iframe = document.createElement('iframe');
        iframe.src = marketUrl;
        document.body.appendChild(iframe);
        document.body.removeChild(iframe);

        setTimeout(function() {
            var cur = Date.now();
            if (cur - pre > 500) {
                return;
            }
            if (document.visibilityState == 'hidden') {
                return;
            }
            location.href = httpsUrl;
        }, 300);
    },
    log: function() {
        window.console && window.console.log(Array.prototype.slice.call(arguments, 0));
    },
    logToApi: function(string) {
        var xhr = new XMLHttpRequest();
        xhr.open('POST', 'http://picoluna.com/m/api/log/', true);
        xhr.setRequestHeader('Content-type', Object.prototype.toString.call(string) == '[object Object]' ? 'application/json' : 'application/x-www-form-urlencoded');
        xhr.send(JSON.stringify(string));
    },
    jsBridge: function(path) {
        setTimeout(function() {
            var iframe = document.createElement('iframe');
            iframe.src = 'jsbridge://whatscall.cmcm.com' + path;
            document.body.appendChild(iframe);
            document.body.removeChild(iframe);
        }, 1);
    },
    clientBridge: function(path) {
        setTimeout(function() {
            var iframe = document.createElement('iframe');
            iframe.src = 'jsbridge://' + path;
            document.body.appendChild(iframe);
            document.body.removeChild(iframe);
        }, 1);
    },
    probability: function(tenThousand) {
        tenThousand = parseInt(tenThousand || 0, 10);
        return Math.random() * 10000 < tenThousand;
    },
    isNetWorkAvailable: function(fn) {
        var xhr = new XMLHttpRequest();
        xhr.open('HEAD', '', true);
        xhr.onload = function(event) {
            fn && fn(true);
        };
        xhr.onerror = function(event) {
            fn && fn(false);
        };
        xhr.send();
    },
    getWscApi: function(path) {
        return (mExp.isTest() ? 'http://whatscall-api.cmcm.com' : 'https://whatscall-api.cmcm.com') + (path ? path : '');
    },
    removeQuery: function(url, name) {
        var URL = String(url);
        var regex = new RegExp("\\?" + name + "=[^&]*&?", "gi");
        URL = URL.replace(regex, '?');
        regex = new RegExp("\\&" + name + "=[^&]*&?", "gi");
        URL = URL.replace(regex, '&');
        URL = URL.replace(/(\?|&)$/, '');
        regex = null;
        return URL;
    },
    isValidDate: function(d) {
        if (Object.prototype.toString.call(d) === '[object Date]') {
            return !isNaN(d.getTime());
        }
        return false;
    }
};

module.exports = mExp;



